.player {
  width: 300px;
  height: 50px;

  display: flex;
  align-items: center;
  gap: 10px;
  padding: 0 10px;

  border: 1px solid #aaa;
}

.player__button {
  width: 34px;
  height: 34px;
  position: relative;


  border: 0;
  border-radius: 50%;
  background-size: 50%;
  background-color: #feac00;
  background-repeat: no-repeat;

  // -webkit-mask-image: url('../images/ui/play.svg');
  // -webkit-mask-repeat: no-repeat;
  // -webkit-mask-position: center;
  // -webkit-mask-size: 70%;

  &_play {
    background-position: 10px 8px;
    background-image: url('../images/ui/play3.svg');
  }
  &_pause {
    background-position: 9px 8px;
    background-image: url('../images/ui/pause2.svg');
  }

  &:hover {
    background-color: #f3a335;
    cursor: pointer;
    filter: brightness(1.1);
  }
}

.player__timeline {
  width: 200px;
  height: 30px;
}

.player__progress {
  width: 100%;
  height: 100%;
  background: #f3a335;
}