input[type=range]::-webkit-slider-runnable-track  {
  height: 10px;
  -webkit-appearance: none;
  color: #27bb13;
  margin-top: -1px;
}

input[type='range'] {
  overflow: hidden;
  width: 100px;
  -webkit-appearance: none;
  background-color: #9a905d;
}

input[type='range']::-webkit-slider-runnable-track {
  height: 10px;
  -webkit-appearance: none;
  color: #13bba54b;
  margin-top: -1px;
}

// thumb 
input[type='range']::-webkit-slider-thumb {
  width: 1px;
  -webkit-appearance: none;
  height: 10px;
  cursor: ew-resize;
  background: #c4830c;
  box-shadow: -80px 0 0 80px #43e5f7;
}