* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  // transition: 0.5s;
}

html {
  font-family: "Roboto";
  font-style: normal;
  // background-color: #0e1621;
  // background-color: #1b1b1b;
  scroll-behavior: smooth;
  // color: #ababab;
  // color: #ffffff;
}

body {
  position: relative;
  overflow-x: hidden;
  width: 100%;
  margin: 0 auto;
  // @media screen and (max-width: 320px) { 
  //   width: 320px;
  // }
}

ul {
  list-style: none;
}

.unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.stop-scroll {
  overflow: hidden;
}